body {
  font-family: "Roboto", sans-serif;
  margin: 0;

  &.modal-open {
    overflow: hidden;
  }
}

button, input {
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: 0;
  outline: none;
}
input[type="checkbox"] {
    display: none;
}
a {
  text-decoration: none;
}
ul {
margin: 0;
padding: 0;
list-style: none;
}
.container {
  margin: 0 auto;
  padding: 0 20px;

  &--medium {
    max-width: 1420px;
    @media (max-width: 440px) {
      padding: 0 10px;
    }
  }

  &--small {
    max-width: 880px;
    padding: 0;
  }

  &--seo {
    max-width: 1180px;
  }
}
